@font-face {
  font-family: 'GolosText';
  src: url('../fonts/GolosText/regular.woff') format('woff'),
       url('../fonts/commissioner/regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'GolosText';
  src: url('../fonts/GolosText/medium.woff') format('woff'),
       url('../fonts/commissioner/medium.woff2') format('woff2');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'GolosText';
  src: url('../fonts/GolosText/semibold.woff') format('woff'),
       url('../fonts/commissioner/semibold.woff2') format('woff2');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'GolosText';
  src: url('../fonts/GolosText/bold.woff') format('woff'),
       url('../fonts/commissioner/bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'Merriweather';
  src: url('../fonts/Merriweather/regular.woff') format('woff'),
       url('../fonts/commissioner/regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Merriweather';
  src: url('../fonts/Merriweather/bold.woff') format('woff'),
       url('../fonts/commissioner/bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}


html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    font-size: 100%;
    height: 100%;
}

body {
    font-family: 'GolosText', sans-serfif;
    font-weight: 400;
    background: white;
    color: #313131;
    height: 100%;
    margin: 0;
    padding: 0;
    box-sizing:border-box;
    min-width: 320px;
}

img {
    max-width: 100%;
    pointer-events: none;
}
p{
    line-height: 1.2em;
}
a {
    text-decoration: none;
    cursor: pointer;
    outline: none;
    color:#AF9470;
    &:visited,
    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        outline: none;
    }
}
.wrap {
    min-width: $min-container-width;
    max-width: $max-container-width;
    box-sizing: border-box;
    margin: 0 auto;
    padding-right: 72px;
    padding-left: 72px;
    @media screen and (max-width: $sm){
      padding-right: 20px;
      padding-left: 20px;
    }
}
.clearfix:after {
    content: "";
    display: table;
    clear: both;
}
::selection {
    color: white;
    background:#524947;
}
.redtextb{
    font-weight: bold;
    color:#F54932;
}
.heading{
    margin: 0;
    font-family: 'Merriweather';
    font-size: 54px;
    line-height: 1.2em;
    font-weight: 300;
    text-transform: uppercase;
    @media screen and (max-width: $sm){
        font-size: 36px;
        line-height: 1.1em;
    }
}
.sm-none{
    @media screen and (max-width: $sm){
        display: none;
    }
}

img {
    opacity: 1;
    transition: opacity 0.3s;
}
img[data-src] {
    opacity: 0;
}
