.main{
  background: #E9DED4 url('../img/bg.webp');
  background-size: 100%;
  background-position: 50% 70%;
  padding:28px 0 129px;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: $sm){
     padding:28px 0 275px;
  }
  &__logo{
    display: inline-block;
    @media screen and (max-width: $sm){
      width: 213px;
    }
  }
  .wrap{
    position: relative;
    z-index: 2;

  }
  &__title{
    font-family: 'Merriweather';
    font-weight: 400;
    text-transform: uppercase;
    font-size: 60px;
    margin: 61px 0 25px;
    line-height: 1.1em;
    @media screen and (max-width: $sm){
      font-size: 36px;
      margin: 41px 0 25px;
    }
    &-offset{
      max-width: 550px;
      display: inline-block;
      text-align: center;
      margin-left: 3px;
      span{
        margin-left: 30px;
      }
    }
  
  }
  &__description{
    font-size: 18px;
    max-width: 542px;
    text-align: center;
    margin-bottom: 42px;
    @media screen and (max-width: $sm){
      text-align: left;
      margin-bottom: 35px;
    }
  }
  &__circle{
    position: absolute;
    top: -36px;
    width: 659px;
    right: -639px;
    left:0;
    margin: auto;
    animation: rotate 40s linear infinite;
    @media screen and (max-width: $md){
      right: -839px;
    }
    @media screen and (max-width: $sm){
      top: auto;
      width: 310px;
      right: 0;
      bottom: 55px;
      left: 0;
      margin: auto;
    }

  }
  &__collage{
    position: absolute;
    bottom: -1px;
    left: 0;
    right: -19px;
    background: url(../img/chessfield.webp);
    background-position:50% 100%;
    background-repeat: repeat;
    background-size: auto;
    height: 540px;
    background-size: 1627px;
    background-position: 50% 100%;
    background-repeat: no-repeat;
    @media screen and (max-width: $md){
      right: -299px;
    }
    @media screen and (max-width: $sm){
      right: 0;
      background-size: 862px;
      background-position: 80% 100%;
    }
  }
  &__btns{
    font-size: 0;
  }
  .btn{
    width: 262px;
    margin-right: 17px;
    @media screen and (max-width: $sm){
      width: 100%;
      margin-right: 0;
      margin-bottom: 14px;
    }
  }
}
.running-line{
    font-family: 'Merriweather';
    color:white;
    position: relative;
    font-weight: 300;
    line-height: 60px;
    width: 100%;
    height: 60px;
    font-size: 22px;
    text-transform: uppercase;
    background:#F54932;
    overflow-x: hidden;
    @media screen and (max-width: $sm){
      font-size: 16px;
      line-height: 38px;
      height: 38px;
    }
    &__track {
      width:fit-content;
      white-space: nowrap;
      
      span::before{
        content: "";
        width: 6px;
        height: 6px;
        display: inline-block;
        background: #fff;
        vertical-align: middle;
        border-radius: 50%;
        margin: 0 20px;
        @media screen and (max-width: $sm){
          margin:0 12px;
        }
      }
    }
    &__content{
       display: inline-block;
    }
}
.info{
  margin-bottom: 204px;
  @media screen and (max-width: 1024px){
      margin-bottom: 104px;
  }
  @media screen and (max-width: $sm){
      margin-bottom: 84px;
  }    
  &__textwp{
    padding: 109px 520px 61px 72px;
    position: relative;
    @media screen and (max-width: $md){
      padding: 109px 370px 61px 72px;
    }
    @media screen and (max-width: 1024px){
      padding: 79px 72px 21px;
    }
    @media screen and (max-width: $sm){
      padding: 50px 20px 20px 20px;
    }
  }
  &__textsp{
    font-family: Merriweather;
    font-size: 36px;
    font-weight: 300;
    text-transform: uppercase;
    margin:0;
    line-height: 1.35em;
    @media screen and (max-width: $sm){
      font-size: 28px;
    }
  }
  &__photo{
    position: absolute;
    right:72px;
    bottom: 0;
    top: 46px;
    margin: auto;
    @media screen and (max-width: $md){
      width: 280px;
    }
    @media screen and (max-width: 1024px){
      position: static;
      display: block;
      margin:50px auto;
      width: auto;
      max-width: 580px;
    }
    @media screen and (max-width: $sm){
      width: 100%;
      margin:23px auto 24px;
    }
  }
  &__collage{
    position: absolute;
    left: 43px;
    top: 2px;
    @media screen and (max-width: $md){
      width: 380px;
    }
    @media screen and (max-width: 1024px){
      position: static;
      display: block;
      margin:20px auto;
      width: auto;
      max-width: 580px;
    }
    @media screen and (max-width: $sm){
      width: 100%;
      margin-bottom: 49px;
    }
  }
  &__session{
    position: relative;
    padding: 32px 72px 0 590px;
    .info__textsp{
      margin-bottom: 60px;
      @media screen and (max-width: $sm){
        margin-bottom: 41px;
      }
    }
    @media screen and (max-width: $md){
      padding: 32px 72px 0 450px;
    }
    @media screen and (max-width: 1024px){
      padding:8px 72px 0 72px;
    }
    @media screen and (max-width: $sm){
      padding: 10px 20px 20px 20px;
    }
  }
  &__table{
    width: 100%;
    border-collapse: collapse;
    font-size: 20px;
    margin-bottom: 48px;
    &-row{
      display: flex;
      &:last-child{
        @media screen and (max-width: $sm){
          border-bottom: 0;
        }
      }
      @media screen and (max-width: $sm){
        display: block;
        border-bottom: 2px solid #D0D0D0;
        margin-bottom: 15px;
      }
    }
    &-cell{
      border: 1px solid #D0D0D0;
      padding:15.5px 0;
      @media screen and (max-width: $sm){
        display: inline;
        border: none;
        padding:0;
      }

    }
    &-row:first-child div{
      border-top:none;
      padding-top:0;

    }
    &-row:last-child div{
      border-bottom:none;
      padding-bottom:0;
    }
    &-row div:first-child{
      border-left: none;
      @media screen and (max-width: $sm){
        display: block;
        margin-bottom: 14px;
      }
    }
    &-row div:last-child{
      border-right: none;
      padding-left:17px;
      width: calc(100% - 305px);
      @media screen and (max-width: $sm){
        display: inline-block;
        padding-left:0;
        width: auto;
        margin-bottom: 14px;
      }
    }
    &-row div:first-child{
      color:#6F6F6F;
      font-weight: 300;
      min-width: 305px;
      @media screen and (max-width: $sm){
        width: auto;
      }
    }
    &-row div:last-child{
      font-weight: 500;
    }
    &-blue{
      color: #3057A2;
      font-size: 20px;
      @media screen and (max-width: $sm){
        font-size: 18px;
      }
    }
    &-oldprice{
      position: relative;
      margin-right: 7px;
      &:before{
        content:'';
        position: absolute;
        display: block;
        width: 100%;
        height: 2px;
        background: #F54932;
        top: 4px;
        left: 0;
        bottom: 0;
        margin: auto;
      }
    }
  }
}

.stages{
  margin-bottom: 200px;
  @media screen and (max-width: $sm){
    margin-bottom: 122px;
  }
  &__wrap{
    font-size: 20px;
    display: grid;
    grid-template-columns: 32.3% 32.3% auto;
    grid-template-rows: auto auto auto;
    gap: 20px;
    counter-reset: example 0;
    position: relative;
    @media screen and (max-width: 1024px){
      grid-template-columns: 48.4% 48.4%;
    }
    @media screen and (max-width: $sm){
      display: flex;
      align-items: flex-start;
      transition:all .2s ease;
    }
  }
  &__content{
    position: relative;
    @media screen and (max-width: $sm){
      padding-top:140px;
      overflow: hidden;
    }
    &:before{
      content: "";
      display: block;
      width: 401px;
      height: 235px;
      background: url(../img/collage1.webp);
      background-size: 100%;
      background-repeat: no-repeat;
      position: absolute;
      bottom: -2px;
      right: -64px;
      z-index: 2;
      @media screen and (max-width: $md){
        bottom: -92px;
      }
      @media screen and (max-width: 1024px){
        right: -70px;
        bottom: -152px;
      }
      @media screen and (max-width: $sm){
        top: 0;
        bottom: auto;
        left:0;
        right:0;
        margin:auto;
        width: 350px;
      }
    }
  }
  .heading{
    margin-bottom: 52px;
    @media screen and (max-width: $sm){
      margin-bottom: 0;
    }
  }
  .text-blue{
    font-size: 20px;
    color: #3057A2;
    text-transform: none;
    font-family: 'GolosText';
    position: relative;
    line-height: 1.2em;
    display: inline-block;
    margin: 0 0 0 6px;
    @media screen and (max-width: $sm){
      font-size: 18px;
      margin:14px 0 0;
    }
  }
  &__item{
    background: url('../img/paper.jpg');
    background-size: cover;
    padding: 20px 22px 20px 72px;
    box-sizing: border-box;
    counter-increment: example 1;
    position: relative;
    font-size: 20px;
    @media screen and (max-width: $sm){
      padding: 64px 22px 50px 72px;
      font-size: 18px;
      min-width: 100%;
    }
    &:after{
      content:counter(example);
      width: 36px;
      height: 36px;
      background: #fff;
      position: absolute;
      top: 20px;
      left: 20px;
      text-align: center;
      line-height: 36px;
      border-radius: 50%;
      font-weight: 500;
      @media screen and (max-width: $sm){
        top: 62px;
        left: 18px;
      }
    
    }
    &:nth-child(3){
      grid-area: 1/3/3;
      @media screen and (max-width: 1024px){
        grid-area: auto;
      }
    }
    &:last-child{
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 3;
      grid-row-end: 3;
      padding-right: 310px;
      @media screen and (max-width: $md){
        padding-right: 100px;
      }
      @media screen and (max-width: 1024px){
        padding-right: 50px;
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 4;
        grid-row-end: 4;
      }
      @media screen and (max-width: $sm){
        padding-right: 22px;
      }
    }
    
  }
  &__text{
    margin: 0;
  }
}
.participants{
  margin-bottom: 140px;
  @media screen and (max-width: $sm){
      margin-bottom: 100px;
    }
  .wrap{
    position: relative;
  }
  &__heading{
    margin-bottom: 60px;
    padding-right: 200px;
    @media screen and (max-width: $sm){
      margin-bottom: 38px;
    }
  }
  &__container{
    overflow:hidden;
  }
  &__item{
    text-align: center;
    min-width: 33.3%;
    padding-bottom: 10px;
  }
  &__track{
    display: flex;
    transition: all .2s;
  }
  &__ava{
    margin-bottom: 22px;
    height: 320px;
    @media screen and (max-width: $sm){
      height: 245px;
      margin-bottom: 29px;
    }
  }
  &__name{
    font-size: 24px;
    font-weight: 600;
    margin:0 0 6px;
  }
  &__status{
    font-size: 20px;
    font-weight: 300;
    margin:0 0 20px;
  }
  
}

.slider{
  &__navigation{
    display: flex;
    align-items: center;
    position: absolute;
    top:8px;
    right:72px;
    @media screen and (max-width: $sm){
      position: static;
      display: flex;
      justify-content: center;
      margin: 40px 0 0;

    }
    button{
      width: 44px;
      height: 44px;
      background: #313131;
      border-radius: 50%;
      outline: none;
      border: none;
      cursor: pointer;
      transition: all .2s ease;
      position: relative;
      @media screen and (max-width: $sm){
        width: 36px;
        height: 36px;
      }
      &:last-child{
        transform: rotate(180deg);
      }
      &:before{
        content:'';
        display: block;
        width: 30px;
        height: 30px;
        background: url('../img/arrow.svg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
        @media screen and (max-width: $sm){
          width: 20px;
          height: 14px;
        }
      }
      &:hover{
        background: #FBCE51;
      }
      &:disabled{
        background: #D6D6D6;
        cursor: auto;
        pointer-events: none;
      }
    }

  }
  &__info{
    margin:0 13px;
  }
  &__dots{
    display: flex;
    margin: 0 10px;
    &-item{
      width: 10px;
      height: 10px;
      background: #d9d9d9;
      margin: 0 3px;
      border-radius: 50%;
      &.active{
        background: #313131;
      }
    }
  }
}
.stages__navigation{
    display: none;
    @media screen and (max-width: $sm){
      display: flex;
      margin-top: 26px;
    }
}
.mfooter{
  background: #E9DED4;
  padding:40px 0 72px;
  &__text{
    margin: 0;
    opacity: .5;
    font-size: 16px;
  }
}
@keyframes rotate {
  0%{
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}