.btn {
    display: inline-block;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    font-size: 16px;
    white-space: nowrap;
    position: relative;
    border:none;
    font-weight: 300;
    transition:all .1s linear;
    font-weight: 400;
    border-radius: 40px;
    box-sizing: border-box;
    line-height: 1.35em;
    &:hover{
        box-shadow:none;
    }
    &__default{
        background: #1F1F1F;
        color:white;
        padding:13px 20px;
        border:2px solid #1F1F1F;
        &:hover{
            background: #FBCE51;
            border:2px solid #FBCE51;
            color:#1F1F1F;
        }
        @media screen and (max-width: $sm){
            padding:11px 20px;
        }
    }
    &__add{
        background: transparent;
        color:#1F1F1F;
        padding:13px 20px;
        border:2px solid #1F1F1F;
        @media screen and (max-width: $sm){
            padding:13px 20px;
            border:none;
            background: rgba(255,255,255,0.9);
        }
        &:hover{
            background: #1F1F1F;
            color:white;
        }
    }
    &__smallblue{
        color:#3057A2;
        border-radius: 20px;
        border:1px solid #3057A2;
        padding: 6px 11px;
        &:hover{
            background:#3057A2;
            color:white;
        }
    }
    
}